import React from "react";
import ReactPlayer from "react-player";
import "./WistiaVideo.css";
import useWindowDimensions from "../useWindowDimensions.js";
export default function WistiaVideo(props) {
  const { width } = useWindowDimensions();

  let widthReducingFactor = 0.45;

  if (width >= 1600) {
    widthReducingFactor = 0.45;
  } else if (width > 1530) {
    widthReducingFactor = 0.42;
  } else if (width > 1350) {
    widthReducingFactor = 0.52;
  } else if (width > 1130) {
    widthReducingFactor = 0.5;
  } else if (width > 930) {
    widthReducingFactor = 0.53;
  } else if (width > 800) {
    widthReducingFactor = 0.52;
  } else {
    widthReducingFactor = 0.42;
  }

  return (
    <div className="player-wrapper transition-all">
      <ReactPlayer
        width={`${width * widthReducingFactor}px`}
        height={`${(width * widthReducingFactor) / 1.82}px`}
        className="react-player"
        url={props.videoLink}
        style={{maxWidth: "810px", maxHeight:"457px"}}
      />
    </div>
  );
}
