import styles from "./OurServices.module.css";

export default function OurServices() {
  const services = [
    {
      title: "Video Ad",
      description:
        "Communicating your ideas into vision of our in house crafted videos.",
      icon: "fa-globe",
      className: "feature_box_col_one",
    },
    {
      title: "Logo Design",
      description:
        "Designing the identity of your brand with the best message in simplest form.",
      icon: "fa-anchor",
      className: "feature_box_col_two",
    },
    {
      title: "Merch Design",
      description:
        "Merchandise is what displays your brand in real world, let's make it crazy.",
      icon: "fa-hourglass-half",
      className: "feature_box_col_three",
    },
    {
      title: "3D Designs",
      description:
        "Not everything amazing is felt in real, virtual 3D designs can be equally good.",
      icon: "fa-database",
      className: "feature_box_col_four",
    },
    {
      title: "Motion Video",
      description:
        "Best and the most cost efficient way to have your message sent out.",
      icon: "fa-upload",
      className: "feature_box_col_five",
    },
    {
      title: "Packaging Design",
      description:
        "Best products not in best packaging doesn't sell best.",
      icon: "fa-camera",
      className: "feature_box_col_six",
    },
  ];

  return (
    <div
      className={`${styles.feat} ${styles["bg-gray"]} ${styles["pt-5"]} ${styles["pb-5"]}`}
    >
      <div className={`${styles.container}`}>
        <div className={`${styles.row}`}>
          <div className={`${styles["section-head"]} ${styles["col-sm-12"]}`}>
            <h4>
              <span>Why Choose</span> Us?
            </h4>
            <p></p>
          </div>

          {services.map((service, index) => (
            <div
              key={index}
              className={`${styles["col-lg-4"]} ${styles["col-sm-6"]}`}
            >
              <div className={`${styles["item"]}`}>
                <span
                  className={`${styles["icon"]} ${styles[service.className]}`}
                >
                  <i className={`fa ${service.icon}`}></i>
                </span>
                <h6>{service.title}</h6>
                <p>{service.description}</p>
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
}
